import React,{useState} from 'react'

function Mainpage(props) {
const handleExtraSpaces = () => {
  let newText = text.split(/[ ]+/)
  setText(newText.join(' '))
  props.showAlert("Removed  Extra Space","info")
}

  const handleCopy = () => {
    console.log("copy the text function runs")
    let textID =  document.getElementById('textID')
    textID.select();
    navigator.clipboard.writeText(textID.value);
    props.showAlert("Copyed the text","info")
  }
  const handleClear  = () => {
      console.log("upper case function runs")
      let newText = "";
      setText(newText)
      props.showAlert("Cleared the text","danger")
  }
  const handleLwCase  = () => {
      console.log("upper case function runs")
      let newText = text.toLowerCase();
      setText(newText)
      props.showAlert("Changed to Lower Case","primary")
  }
  const handleUpCase  = () => {
      console.log("upper case function runs")
      let newText = text.toUpperCase();
      setText(newText)
      props.showAlert("Changed to Upper Case","primary")
  }
  const handleOnChange = (event) =>{
    console.log('om change')
    setText(event.target.value)
  }
  const [text, setText] = useState("");
  return (
    <>
  <div className='container'>

  <h1 className='text-primary'><i> {props.heading} </i> </h1>
  <div className="mb-3">
  <label htmlFor="exampleFormControlTextarea1" className="form-label">Here You can Type..</label>
  <textarea className="text-light fs-1 form-control p-3 bg-info bg-opacity-10 border border-info border-start-0 rounded-end border-5" id="textID" rows="3"placeholder='Enter Your Text Here..' value={text} onChange={handleOnChange}></textarea>
</div>
<button type="button" className="btn btn-primary px-4 fs-5 mx-2" onClick={handleUpCase}>Upper   Case</button> <button type="button" className="btn btn-success px-4 fs-5 mx-2" onClick={handleLwCase}>Lower Case</button>
<button type="button" className="btn btn-primary px-4 fs-5 mx-2" onClick={handleClear}>Clear text</button>
<button type="button" className="btn btn-primary px-4 fs-5 mx-2" onClick={handleCopy}>Copy text</button>
<button type="button" className="btn btn-primary px-4 fs-5 mx-2" onClick={handleExtraSpaces}>Remove Extra Spaces</button>
  </div>
  <div className='container my-3'>
    <h2>Text Summary</h2>
    <p>{text.split(' ').length} and {text.length} charecters</p>
    <p>{0.008 * text.split(" ").length} Minutes To Read</p>
    <h3 className='text-success'>Preview</h3>
    <p>{text}</p>
  </div>
  </>
  )
}



export default Mainpage
